import React from 'react';

class CaricaWindow extends React.Component {


	componentDidMount(){


    //window.eccol.addEventListener("keydown", this.funzioneKBEventEccolo)
    //window.freim.addEventListener("load", this.freimCaricato)
    window.divfiglio.addEventListener("change", this.divfiglioCaricato)
    window.addEventListener("load", this.consoleEvent)

    var form = document.forms.namedItem("fileinfo");

	form.addEventListener('submit', this.formSubmit, false);



	}

	constructor(){
		super();
		this.state={
			link:'',
			tag:'',
			stato:null,
			caric:"displayNone",
			minimized:false,

		}
	}

	consoleEvent = (event) =>{
		console.log(event)
	}

	onLinkChange = (event) => {


		var preview = document.getElementById('imgSfondoImg');
  	var file    = document.getElementById('avatar').files[0];
  	var reader  = new FileReader();

		reader.addEventListener("load", function () {
    preview.src = reader.result;
		//window.caricaWindow.style.backgroundImage = `url(${reader.result})`;
  	}, false);
		if (file) {
    reader.readAsDataURL(file);
		}else{
			preview.src='';
		}

		//window.caricaWindow.style.backgroundImage = "url(event.target.value)";

		const pos = event.target.value.indexOf(`fakepath`);
		this.setState({link:event.target.value.slice(pos+9)});

	}

	onTagChange = (event) => {
		this.setState({tag:event.target.value})
	}



	onSubmitFoto = () =>{
		if(this.state.link===''||this.state.tag===''){

		}else{
			//console.log(this.props.lastRobotId()+1)
			let robotsLen = 0;
			if((this.state.tag.search('nascondi'))>=0){
				robotsLen = (this.props.lastRobotId()+1)*(-1);
			}else{
				robotsLen = (this.props.lastRobotId()+1)*(1);
			}

			fetch('https://agile-forest-45123.herokuapp.com/fotoput',{
				method:'PUT',
				headers:{'Content-Type':'application/json'},
				body: JSON.stringify({link:`https://dolfo.altervista.org/verso/images/${this.state.link}`, tag:`show ${this.state.tag}`, robotsLength:robotsLen})
			})
			.then(res=>{

				//console.log(res)
				 this.props.fetchFoto();
				 window.location.reload();//da fixare (reload when upload something)
			})
		}
		
    }

    funzioneKBEventEccolo = (event) => {

	    if(this.state.type==="mobile"&&this.state.access !== 'negato'&&event.which===13)
      		window.barracerca.blur();


  	}

  	showCaricamento = (event) => {
	    if(this.state.link===''||this.state.tag===''){

		}else{
	    	this.setState({caric:"displayInline"})

	    	if(window.buttonInvia.innerHTML!=="Annulla"){
	    		window.buttonInvia.innerHTML="Annulla";

	    		}else{
	    			window.buttonInvia.innerHTML="Invia";
	    			setTimeout(this.props.nonCarica, 500);


	    		}

	    	if(!window.divfiglio.classList.contains("bg-white-30"))
	    	window.divfiglio.classList.toggle("bg-white-30")
				}

    }

  	nonCaricaLate = (stringa) => {
			if(stringa!=='abort')
  			this.onSubmitFoto();
			window.avatar.value = null;
			window.lastname.value = null;

			setTimeout(this.cleanBar, 500);

  	}

		cleanBar = () => {
			window.divfiglio.innerHTML='';
			window.divfiglio.style.width = '0%';
		}

  	freimCaricato = (event) => {
	    if(this.state.link===''||this.state.tag===''){

		}else{


		    this.onSubmitFoto();
		    this.props.nonCarica();
		}

  	}

  	divfiglioCaricato = (event) => {
  		console.log(event)
  	}

  	formSubmit = (ev) => {

			if(window.divfiglio.style.width==="0%"){
						var form = document.forms.namedItem("fileinfo");
						console.log("nuovo xhr")
						var oData = new FormData(form);

						var oReq = new XMLHttpRequest();
						oReq.open("POST", "https://dolfo.altervista.org/verso/carica.php", true);
						//oReq.setRequestHeader("Access-Control-Allow-Origin", "*");

						oReq.upload.addEventListener("progress", progressHandler, false);
						oReq.addEventListener("error", errorHandler, false);
						oReq.addEventListener("abort", abortHandler, false);
						oReq.addEventListener("load", completeHandler, false);
						var gino = this.nonCaricaLate;
						oReq.addEventListener("load", gino, false);
						window.buttonInvia.addEventListener("click", function(){oReq.abort();})
						//window.buttonIndietro.addEventListener("click", function(){oReq.abort();})

						function abortHandler(event){

							oReq.abort();
							//show Errore
							window.divfiglio.innerHTML=`annullato`;

							gino('abort');



							console.log("caricamento annullato")
						}

						function completeHandler(event){
							//show Errore
							window.divfiglio.innerHTML=`caricamento completato`;
							window.buttonInvia.innerHTML="Invia";
							var preview = document.getElementById('imgSfondoImg');
							preview.src='';

							console.log("caricamento completato")
							//dopo un secondo reindirizza dietro
						}

						function errorHandler(event){
							window.divfiglio.innerHTML=`errore nel caricamento`;
							console.log("errore nel caricamento")

						}

						function progressHandler(event) {

							//_("loaded_n_total").innerHTML = "Uploaded " + event.loaded + " bytes of " + event.total;
							var percent = (event.loaded / event.total) * 100;
							window.divfiglio.style.width=`${percent}%`;
							if(Math.round(percent)===100)
								window.divfiglio.innerHTML=`99%`;
							else
								window.divfiglio.innerHTML=`${Math.round(percent)}%`;

						}

						oReq.send(oData);
						ev.preventDefault();
					}
	}
	maximize = () =>{
		let window = document.getElementById(`caricaWindow`);
		let imgSfondo = document.getElementById(`imgSfondo`);

		//immagine.classList.toggle("displayNone");
		if(!window.classList.contains("caricaWindowMinimizedMobile"))
			window.classList.add("caricaWindowMinimizedMobile");
			else
				window.classList.remove("caricaWindowMinimizedMobile");
		if(!imgSfondo.classList.contains("imgSfondoDrop"))
			imgSfondo.classList.add("imgSfondoDrop");
			else
			imgSfondo.classList.remove("imgSfondoDrop");
			this.setState({minimized:false})
	}
	minimize = () =>{
		let window = document.getElementById(`caricaWindow`);
		let imgSfondo = document.getElementById(`imgSfondo`);

		//immagine.classList.toggle("displayNone");
		if(!window.classList.contains("caricaWindowMinimizedMobile"))
			window.classList.add("caricaWindowMinimizedMobile");
			else
				window.classList.remove("caricaWindowMinimizedMobile");
		if(!imgSfondo.classList.contains("imgSfondoDrop"))
			imgSfondo.classList.add("imgSfondoDrop");
			else
			imgSfondo.classList.remove("imgSfondoDrop");

			this.setState({minimized:true})
	}

	closeW = () =>{
		let window = document.getElementById(`caricaWindow`);

		//immagine.classList.toggle("displayNone");
			window.classList.add("caricaWindowOpenMobile");

			this.setState({minimized:false})
	}

	/*if(props.props.check===0&&props.props.type!=='mobile'){
		console.log("0&&!==mobile")
		return ();
	}else if(props.props.type==='mobile'){
		if(props.props.check===0){
			console.log("0&&===mobile")
			return ();
		}else{
			console.log("1&&===mobile")
			return ();
		}
	} else {
		if(window.innerWidth>= 1057){
			console.log("0&&!==mobile>=1057")
								return ();
							}else{
								console.log("1&&!==mobile<=1057")
								return ();
							}
						}*/
						render(){
							//console.log(this.props.state)
						if(window.innerWidth>= 1057&&
						this.props.state.type==='desk'
						){
							return(
								<div id="caricaWindow" className="caricaWindowStartDesk caricaWindowOpenMobile ">
 									<form name="fileinfo" id="eccol" encType="multipart/form-data" action="https://dolfo.altervista.org/verso/carica.php" method="POST" target="iframe_a" className="" >

													 <div className=" black-80  ba b--transparent">
													   <div className=" black-80  ba b--transparent alignRight">
															 {!this.state.minimized
																 ? <img onClick={this.minimize} id="minimize" src="https://dolfo.altervista.org/verso/images/ilviaggio/minimize.png" alt="" height="20px" width="20px"></img>
																 : <img onClick={this.maximize} id="maximize" src="https://dolfo.altervista.org/verso/images/ilviaggio/maximize.png" alt="" height="20px" width="20px"></img>

															 }

															 <img onClick={this.closeW} id="closeW" src="https://dolfo.altervista.org/verso/images/ilviaggio/closeW.png" alt="" height="20px" width="20px"></img>
														 </div>
														 <div id="imgSfondo" className="imgSfondo">
															 <img id="imgSfondoImg" src="" alt="" ></img>
														 </div>
														 <div style={{lineHeight: '0.01'}} id="divpapa" className={/*this.state.caric+*/"  barraCaricamentoMobile"}>
															 <div id="divfiglio" style={{width:'0%', padding: '10px'}} className=" figlioBorder b"></div>
														 </div>
											       <div className="mt3">
											           <input onChange={this.onLinkChange} type="file" name="avatar" required id="avatar" className=" w-80 pa2 input-reset pointer ba bg-transparent form-control" />
											       </div>
											       <div className="mt3">
											           <label className="db fw6 lh-copy f6" htmlFor="lastname">Tag</label>
											           <input onChange={this.onTagChange} type="text" name="lastname" required id="lastname" className="w-90 mb3 ph3 pv2 input-reset ba b--black bg-transparent dib btn btn-primary form-control"/>
											        </div>
											        <div className="form-group col-md-6">
											            {/*<button id="buttonIndietro" onClick={this.props.nonCarica} className="b ph3 w-40 ml2 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib btn btn-primary" >Indietro</button>*/}
											            <button id="buttonInvia" onClick={this.showCaricamento} className="b ph3 mr2 w-40 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib btn btn-primary" >Invia</button>


											        </div>
											    </div>

											    <iframe src="" id="freim"  className="h4 w-90 displayNone"  name="iframe_a" title="This is a second title"></iframe>

										     </form>









								</div>
							);
						}else{
							return(
								<div id="caricaWindow" className="caricaWindowStartMobile caricaWindowOpenMobile ">
 									<form name="fileinfo" id="eccol" encType="multipart/form-data" action="https://dolfo.altervista.org/verso/carica.php" method="POST" target="iframe_a" className="" >

													 <div className=" black-80  ba b--transparent">
													   <div className=" black-80  ba b--transparent alignRight">
															 {!this.state.minimized
																 ? <img onClick={this.minimize} id="minimize" src="https://dolfo.altervista.org/verso/images/ilviaggio/minimize.png" alt="" height="20px" width="20px"></img>
																 : <img onClick={this.maximize} id="maximize" src="https://dolfo.altervista.org/verso/images/ilviaggio/maximize.png" alt="" height="20px" width="20px"></img>

															 }

															 <img onClick={this.closeW} id="closeW" src="https://dolfo.altervista.org/verso/images/ilviaggio/closeW.png" alt="" height="20px" width="20px"></img>
														 </div>
														 <div id="imgSfondo" className="imgSfondo">
															 <img id="imgSfondoImg" src="" alt="" ></img>
														 </div>
														 <div style={{lineHeight: '0.01'}} id="divpapa" className={/*this.state.caric+*/"  barraCaricamentoMobile"}>
															 <div id="divfiglio" style={{width:'0%', padding: '10px'}} className=" figlioBorder b"></div>
														 </div>
											       <div className="mt3">
											           <input onChange={this.onLinkChange} type="file" name="avatar" required id="avatar" className=" w-80 pa2 input-reset pointer ba bg-transparent form-control" />
											       </div>
											       <div className="mt3">
											           <label className="db fw6 lh-copy f6" htmlFor="lastname">Tag</label>
											           <input onChange={this.onTagChange} type="text" name="lastname" required id="lastname" className="w-90 mb3 ph3 pv2 input-reset ba b--black bg-transparent dib btn btn-primary form-control"/>
											        </div>
											        <div className="form-group col-md-6">
											            {/*<button id="buttonIndietro" onClick={this.props.nonCarica} className="b ph3 w-40 ml2 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib btn btn-primary" >Indietro</button>*/}
											            <button id="buttonInvia" onClick={this.showCaricamento} className="b ph3 mr2 w-40 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib btn btn-primary" >Invia</button>


											        </div>
											    </div>

											    <iframe src="" id="freim"  className="h4 w-90 displayNone"  name="iframe_a" title="This is a second title"></iframe>

										     </form>
											 </div>
							);
						}
					}
};

export default CaricaWindow;



